import React from 'react';
import { Link } from 'react-router-dom';
import { useTrail, animated } from 'react-spring';
import newLinkData, { defaultTrail } from '../../utils/LinkData';
import './style.css';

export default () => {
	const links = newLinkData();
	links.push('/', 'Go to the main pageâ€¦');
	const trail = useTrail(links.data.length, defaultTrail);

	return (
		<>
			<p>The page you're trying to view does not exist! Did you just enter an alternate dimension?</p>
			<nav>
				<ul id="nav-menu">
					{
						trail.map((props, index) => (
							<animated.li key={ links.data[index].key } style={ props }><Link to={ links.data[index].url }>{ links.data[index].text }</Link></animated.li>
						))
					}
				</ul>
			</nav>
		</>
	);
};